"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import AnimateOnView from "@/components/AnimateOnView";

import Client1 from "@/images/logo.png";
import Client2 from "@/images/onlylogo.png";
import Client3 from "@/images/headerlogo.jpeg";
import Client4 from "@/images/cop1.png";
import Client5 from "@/images/cop2.png";
import Client6 from "@/images/cop3.png";
import Client7 from "@/images/cop4.png";

export const metadata = {
  title: "Careers ‚Äî TantraDOT Technologies",
  description: "Join TantraDOT ‚Äî see open roles, internships, and how we hire.",
};

type Job = {
  _id: string;
  title: string;
  description?: string;
  location?: string;
  createdAt: string;
};

export default function CareersPage() {
  const [jobs, setJobs] = useState<Job[]>([]);
  const [query, setQuery] = useState("");
  const [locationFilter, setLocationFilter] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    let mounted = true;
    setLoading(true);
    fetch("/api/jobs")
      .then((res) => res.json())
      .then((data) => {
        if (!mounted) return;
        setJobs(Array.isArray(data) ? data : []);
      })
      .catch((err) => {
        if (!mounted) return;
        setError(String(err));
      })
      .finally(() => mounted && setLoading(false));

    return () => {
      mounted = false;
    };
  }, []);

  const locations = useMemo(() => {
    const set = new Set<string>();
    jobs.forEach((j) => j.location && set.add(j.location));
    return Array.from(set);
  }, [jobs]);

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase();
    return jobs.filter((j) => {
      const matchesQuery =
        !q || j.title.toLowerCase().includes(q) || (j.description || "").toLowerCase().includes(q);
      const matchesLocation = !locationFilter || j.location === locationFilter;
      return matchesQuery && matchesLocation;
    });
  }, [jobs, query, locationFilter]);

  return (
    <main className="space-y-28">
      {/* Hero */}
      <section className="relative overflow-hidden bg-linear-to-b from-yellow-50 via-white to-white py-20 hero-section">
        <div className="max-w-7xl mx-auto px-8 text-center">
          <AnimateOnView animation="animate-zoom-in" delay="80ms">
            <span className="inline-block mb-4 rounded-full bg-yellow-100 px-4 py-1 text-sm font-semibold text-yellow-700">Careers at TantraDOT</span>
          </AnimateOnView>

          <AnimateOnView animation="animate-zoom-in" delay="140ms">
            <h1 className="mt-4 text-4xl md:text-5xl lg:text-5xl font-extrabold tracking-tight text-slate-900 responsive-title">Join Our Team</h1>
          </AnimateOnView>

          <AnimateOnView animation="animate-slide-left" delay="200ms">
            <p className="mt-6 max-w-3xl mx-auto text-lg text-slate-600 leading-relaxed">We're building delightful software ‚Äî come help shape the future with us.</p>
          </AnimateOnView>

          <div className="absolute -top-24 left-1/2 -translate-x-1/2 h-72 w-72 rounded-full bg-yellow-200 opacity-30 blur-3xl" />
        </div>
      </section>

      {/* Job search & listings */}
      <section className="max-w-7xl mx-auto px-8">
        <div className="bg-white shadow-md rounded-lg p-6 -mt-4">
          <div className="flex flex-col md:flex-row gap-4 md:items-center md:justify-between">
            <div className="flex-1">
              <label className="sr-only">Search jobs</label>
              <div className="relative">
                <svg className="w-5 h-5 text-slate-400 absolute left-3 top-1/2 -translate-y-1/2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-4.35-4.35m0 0A7.5 7.5 0 1016.65 16.65z" />
                </svg>
                <input
                  className="pl-10 pr-4 py-3 w-full md:w-96 rounded-lg border border-slate-200 focus:outline-none focus:ring-2 focus:ring-indigo-200"
                  placeholder="Search roles, skills, or keywords"
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                />
              </div>
            </div>

            <div className="flex items-center gap-3">
              <select
                className="rounded-lg border border-slate-200 py-2 px-3 bg-white text-sm"
                value={locationFilter}
                onChange={(e) => setLocationFilter(e.target.value)}
              >
                <option value="">All locations</option>
                {locations.map((loc) => (
                   <option key={loc} value={loc}>{loc}</option>
                ))}
              </select>

              <button
                className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 transition"
                onClick={() => { setQuery(""); setLocationFilter(""); }}
              >
                Reset
              </button>
            </div>
          </div>

          <div className="mt-6">
            {loading ? (
              <p className="text-slate-500">Loading jobs‚Ä¶</p>
            ) : error ? (
              <p className="text-red-500">Failed to load jobs: {error}</p>
            ) : filtered.length === 0 ? (
              <p className="text-slate-600">No job openings match your search.</p>
            ) : (
              <ul className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
                {filtered.map((job, i) => (
                  <AnimateOnView key={job._id} animation={i % 2 === 0 ? "animate-zoom-in" : "animate-zoom-out"} delay={`${i * 80}ms`}>
                    <li className="relative bg-white border border-slate-100 rounded-xl p-5 shadow hover:shadow-lg transition transform hover:-translate-y-1 flip-card">
                      <div className="flip-card-inner">
                        <div className="flex items-start justify-between gap-4">
                          <div className="flex-1">
                            <h3 className="text-lg font-semibold text-slate-900">{job.title}</h3>
                            {job.location && <p className="text-sm text-slate-500 mt-1">üìç {job.location}</p>}
                            {job.description && <p className="mt-3 text-sm text-slate-700 line-clamp-3">{job.description}</p>}
                          </div>

                          <div className="shrink-0 text-right">
                            <p className="text-xs text-slate-400">{new Date(job.createdAt).toLocaleDateString()}</p>
                            <a
                              className="mt-3 inline-block bg-indigo-600 text-white text-sm px-3 py-2 rounded-md hover:bg-indigo-700"
                              href={`mailto:careers@tantradot.com?subject=${encodeURIComponent("Application for " + job.title)}`}
                            >
                              Apply
                            </a>
                          </div>
                        </div>
                      </div>
                    </li>
                  </AnimateOnView>
                ))}
              </ul>
            )}
          </div>
        </div>
      </section>

      {/* Clients marquee */}
      <section className="py-12">
        <div className="max-w-7xl mx-auto px-8 text-center">
          <h3 className="text-2xl font-semibold text-slate-900">Our Clients</h3>
          <p className="text-slate-600">Trusted by teams and companies across industries</p>

          <div className="mt-8 clients-marquee" aria-label="Client logos carousel">
            <div className="marquee-track" role="list">
              {[Client1, Client2, Client3, Client4, Client5, Client6, Client7].concat([Client1, Client2, Client3, Client4, Client5, Client6, Client7]).map((src, idx) => (
                <div className="marquee-item" key={idx} role="listitem" aria-hidden={idx >= 7 ? true : false}>
                  <div className="w-28 h-12 flex items-center justify-center">
                    <img src={src.src || src} alt={`client ${idx + 1}`} width={140} height={48} className="client-logo" />
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* CTA */}
      <AnimateOnView animation="animate-zoom-out" delay="120ms">
        <section className="bg-slate-900 text-white rounded-3xl p-12 text-center max-w-7xl mx-auto px-6">
          <h2 className="text-3xl font-bold">Join the team or refer someone ‚Äî <span className="text-yellow-400">Let‚Äôs talk</span></h2>
          <p className="mt-4 text-slate-300 max-w-2xl mx-auto">If you‚Äôre excited about building products and working with an experienced engineering team, reach out.</p>
          <a href="/contact" className="inline-block mt-8 bg-yellow-400 text-slate-900 px-8 py-3 rounded-md font-semibold hover:bg-yellow-500 transition shadow-md">Contact Us</a>
        </section>
      </AnimateOnView>
    </main>
  );
}
